<template>
  <h3 class="fw-bold text-center mt-5"></h3>
  <div class="row justify-content-center mb-5">
    <div class="col-md-6 border p-3">
      <Form v-slot="{ errors }" @submit="createOrder" ref="form">
        <div class="mb-3">
          <label for="email" class="form-label w-100">Email
            <Field
              id="email"
              name="email"
              type="email"
              class="form-control"
              :class="{ 'is-invalid': errors['email'] }"
              placeholder=" Email"
              rules="email|required"
              v-model="form.user.email"
            ></Field>
            <ErrorMessage
              name="email"
              class="invalid-feedback"
            ></ErrorMessage>
          </label>
        </div>

        <div class="mb-3">
          <label for="name" class="form-label w-100">
            <Field
              id="name"
              name=""
              type="text"
              class="form-control"
              :class="{ 'is-invalid': errors[''] }"
              placeholder=""
              rules="required"
              v-model="form.user.name"
            ></Field>
            <ErrorMessage
              name=""
              class="invalid-feedback"
            ></ErrorMessage>
          </label>
        </div>

        <div class="mb-3">
          <label for="tel" class="form-label w-100">
            <Field
              id="tel"
              name=""
              type="text"
              class="form-control"
              :class="{ 'is-invalid': errors[''] }"
              placeholder=""
              rules="min:8|max:10|required"
              v-model="form.user.tel"
            ></Field>
            <ErrorMessage
              name=""
              class="invalid-feedback"
            ></ErrorMessage>
          </label>
        </div>

        <div class="mb-3">
          <label for="address" class="form-label w-100">
            <Field
              id="address"
              name=""
              type="text"
              class="form-control"
              :class="{ 'is-invalid': errors[''] }"
              placeholder=""
              rules="required"
              v-model="form.user.address"
            ></Field>
            <ErrorMessage
              name=""
              class="invalid-feedback"
            ></ErrorMessage>
          </label>
        </div>

        <div class="mb-3">
          <label for="message" class="form-label w-100">
            <textarea
              name="message"
              id="message"
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </label>
        </div>

        <div class="text-end">
          <button
            type="submit"
            class="btn btn-danger"
            :disabled="
            Object.keys(errors).length > 0 || cartData.carts.length === 0"
          >
            
          </button>
        </div>
      </Form>
    </div>
  </div>
  <Toast ref="toast" />
</template>

<script>
import {
  Form, Field, ErrorMessage, defineRule, configure,
} from 'vee-validate';
import AllRules from '@vee-validate/rules';
import { localize, setLocale } from '@vee-validate/i18n';
import zhTW from '@vee-validate/i18n/dist/locale/zh_TW.json';
import Toast from '@/components/ToastTip.vue';

Object.keys(AllRules).forEach((rule) => {
  if (rule !== 'default') {
    defineRule(rule, AllRules[rule]);
  }
});

configure({
  generateMessage: localize({ zh_TW: zhTW }),
  validateOnInput: true,
});

setLocale('zh_TW');

export default {
  props: ['cartData'],
  emits: ['get-cart'],
  data() {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
    };
  },
  components: {
    Form,
    Field,
    ErrorMessage,
    Toast,
  },
  methods: {
    //  v
    createOrder() {
      const apiUrl = `
        ${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order
      `;
      const order = this.form;

      this.$http.post(apiUrl, { data: order })
        .then((res) => {
          this.$refs.form.resetForm();
          this.form.message = '';
          this.$emit('get-cart'); // 
          this.$refs.toast.openToast(res, '', false);
        })
        .catch((err) => {
          console.dir(err.response.data.message);
        });
    },
  },
  mounted() {
    this.$refs.form.resetForm();
  },
};
</script>
